.collectionItem {
    width: 24%;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
    padding : 10px;
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }

    button{
      width: 60%;
      opacity: 0.7;
      position: absolute;
      top: 255px;
      display: none;
    }

    &:hover{
      .image{
        opacity: 0.8;
      }

      button{
        opacity: 0.85;
        display: block;
      }
    }
  
    .collectionFooter {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
      .name {
        width: 90%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 10%;
      }
    }
  }